header {
  overflow: hidden;
  background: var(--color-bg-variant);
  border: hidden;
}
.header__container {
  height: 100%;
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}
.profile h1 {
    font-size: 25px;
}
/*==================HEADER-SOCIALS=====================*/

.header__icons {
  grid-gap: 0.8rem;
  gap: 0.8rem;
  font-size: 32px;
}

.header__icons::before {
  content: '';
  width: 1px;
  height: 2rem;
  background-color: var(--color-white);
}
/*==================ME=====================*/
.me {
  background: var(--color-bg-variant-hover);
  width: 120px;
  height: 120px;
  margin-inline: 5px;
  border-radius: 50%;
  overflow: hidden;
  padding: 1rem;
}
/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/
@media screen and (max-width: 1280px) {
  .container.about__container {
    display: flex;
    flex-direction: column;
}

}
@media screen and (max-width: 1024px) {
  .header {
    height: 68vh;
  }
.header__socials {
  margin-right: 55px;
}

}

