main {
    overflow: hidden;
    background: transparent;
    border: none;
    margin-top: -2px;
  }
.main__container {
    height: 100%;
    position: relative;
    width: 100%;
  }
  .text-role{
    color: var(--color-primary);
    font-size: 25px;
  }
.intro {
  border-radius: 0px 0px 0px 244px;
  background: var(--color-primary);
  height: 30rem;
  width: 100%;
}
.greay-box-content {
  border-radius: 0px 0px 0px 244px;
  background: var(--color-white);
  box-shadow: inset 0px 0px 20px black;
  position: absolute;
  right: 0;
  width: 50%;
  height: 30rem;
}
.greay-box-content:hover {
  border: 1px solid var(--color-primary-variant);
}
.greay-box-content .overlay {
    border-radius: 0px 0px 0px 244px;
    content: "";
    position: absolute;
    right: 0;
    width: 100%;
    height: 30rem;
    z-index: 2;
    transition: background 0.2s;
  }
  .greay-box-content .overlay {
    background: var(--color-overlay);
  }
  /*==================CTA=====================*/
.cta a {
  margin-bottom: 10px;
  color: var(--color-primary);
}
/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/
@media screen and (max-width: 1280px) {
  .text-role, .text-greetings {
    font-size: 15px;
  }

}
  @media screen and (max-width: 1024px) {
    .main {
      height: 68vh;
    }
 
  }
  
