nav {
  background: var(--color-bg-variant);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;;
  backdrop-filter: blur(15px);
  border: 1px solid transparent;
}
nav .icon-item {
  color: var(--color-white);
}