@keyframes clockwise {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes counter-clockwise {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(-360deg);
    }
  }
  
  .gearbox {
    position: relative;
    width: 62%;
    height: 17rem;
    right: 0rem;
    top: 1rem;
    z-index: 1;
    border: none;
    overflow: hidden;
    border-radius: 6px;
}
  
  .gear {
    position: absolute;
    height: 60px;
    width: 60px;
    box-shadow: 0px -1px 0px 0px var(---color-primary), 0px 1px 0px 0px var(---color-primary);
    border-radius: 30px;
  }
  
  .gear.large {
    height: 120px;
    width: 120px;
    border-radius: 60px;
  }
  
  .gear.large:after {
    height: 96px;
    width: 96px;
    border-radius: 48px;
    margin-left: -48px;
    margin-top: -48px;
  }
  
  .gear.one {
    top: 12px;
    left: 10px;
  }
  
  .gear.two {
    top: 61px;
    left: 60px;
  }
  
  .gear.three {
    top: 110px;
    left: 10px;
  }
  
  .gear.four {
    top: 13px;
    left: 128px;
  }
  
  .gear:after {
    content: "";
    position: absolute;
    height: 36px;
    width: 36px;
    border-radius: 36px;
    background: var(--color-bg-variant);
    top: 50%;
    left: 50%;
    margin-left: -18px;
    margin-top: -18px;
    z-index: 3;
    box-shadow: 0px 0px 10px var(--color-primary), inset 0px 0px 10px var(--color-primary), inset 0px 2px 0px 0px var(--color-primary), inset 0px -1px 0px 0px #888888;
  }
  
  .gear-inner {
    position: relative;
    height: 100%;
    width: 100%;
    background: var(--color-primary);
    border-radius: 30px;
    border: 1px solid var(--color-primary);
  }
  
  .large .gear-inner {
    border-radius: 60px;
  }
  
  .gear.one .gear-inner {
    animation: counter-clockwise 3s infinite linear;
  }
  
  .gear.two .gear-inner {
    animation: clockwise 3s infinite linear;
  }
  
  .gear.three .gear-inner {
    animation: counter-clockwise 3s infinite linear;
  }
  
  .gear.four .gear-inner {
    animation: counter-clockwise 6s infinite linear;
  }
  
  .gear-inner .bar {
    background: var(--color-primary);
    height: 16px;
    width: 76px;
    position: absolute;
    left: 50%;
    margin-left: -38px;
    top: 50%;
    margin-top: -8px;
    border-radius: 2px;
    border-left: 1px solid var(--color-primary);
    border-right: 1px solid var(--color-primary);
  }
  
  .large .gear-inner .bar {
    margin-left: -68px;
    width: 136px;
  }
  
  .gear-inner .bar:nth-child(2) {
    transform: rotate(60deg);
  }
  
  .gear-inner .bar:nth-child(3) {
    transform: rotate(120deg);
  }
  
  .gear-inner .bar:nth-child(4) {
    transform: rotate(90deg);
  }
  
  .gear-inner .bar:nth-child(5) {
    transform: rotate(30deg);
  }
  
  .gear-inner .bar:nth-child(6) {
    transform: rotate(150deg);
  }
  @media screen and (max-width: 912px){
  .gearbox {
    width: 68%;
    right: -1rem;
}
}
  @media screen and (max-width: 820px){
    .gearbox {
      width: 69%;
      right: -1rem;
    }
    }
  @media screen and (max-width: 768px){
    .gearbox {
      width: 80%;
      height: 11rem;
      right: 0rem;
      top: 0rem;
  }
  .gear {
    height: 30px;
    width: 30px;
}
  .gear-inner {
    height: 100%;
    width: 100%;
    border-radius: 50%;
  }
  .gear-inner .bar {
    height: 8px;
    width: 40px;
    margin-left: -20px;
    margin-top: -4px;
}
.gear:after {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  margin-left: -8px;
  margin-top: -7px;
}
.gear.large {
  height: 40px;
  width: 40px;
}
.large .gear-inner .bar {
  margin-left: -25px;
  width: 50px;
  margin-top: -4px;
}
.gear.large:after {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  margin-left: -10px;
  margin-top: -9px;
}
.gear.one {
  top: 73px;
  left: 22px;
}
.gear.two {
  top: 84px;
  left: 57px;
}
.gear.four {
  top: 57px;
  left: 90px;
}
.gear.three {
  top: 120px;
  left: 46px;
}
    }

    @media screen and (max-width: 280px){
    .gearbox {
    width: 100%;
    height: 11rem;
    right: 10px;
    top: 0rem;
}
}
