  /*==================device-content=====================*/
  .device-main-container {
    position: absolute;
    width: 100%;
    top: 43px;
    z-index: 100;
}
  .device-container {
    width: 100%;
}
.arrow-left {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    animation: float 6s ease-in-out infinite;
    color: var(--color-primary);
    margin-top: 5px;
    margin-left: 40px;
    position: fixed;
}
.device-content {
  animation-duration: 3s;
  animation-name: slidein;
  position: relative;
  width: 46%;
  height: 70vh;
  overflow: hidden;
  transition-property: margin-left;
  background: var(--color-white);
  border: 22px solid var(--color-text);
  border-radius: 3%;
  box-shadow: var(--shadow-color);
}
.device-content::after {
  content: '';
  width: 23%;
  height: 6%;
  border-radius: 15px;
  background: var(--color-text);
  display: block;
  position: absolute;
  left: 40%;
  bottom: 8px;
  opacity: 0.5;
}
.content_list {
  display: grid;
  position: absolute;
  height: 96%;
  width: 100%;
  margin-top: 28px;
  z-index: 100;
}
@keyframes slidein {
  from {
    margin-left: 100%;
    transition: var(--transition);
  }

  to {
    margin-left: 0%;
    transition: var(--transition);
  }
}
@keyframes float {
  0% {
    transform: translatex(0px);
  }
  50% {
    transform: translatex(-20px);
  }
  100% {
    transform: translatex(0px);
  }
}
  /*==================Resume-content=====================*/

.headCaption-main, .headCaption {
    color: var(--color-text);
}
.project-details-content {
  position: absolute;
  top: 19%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--color-white);
  border: 2px solid var(--color-text);
  box-shadow: var(--shadow-color);
  width: 90%;
  padding: 20px;
}
#parent-modal-title{
  color: var(--color-text);
}
